import React, { useEffect, useState } from "react";
import { Card } from ".";
import { Link } from "react-router-dom";
export default function Display() {
    const [videos, setVideos] = useState([]);
    const [loading, setLoading] = useState(true);
    useEffect(() => {
        // Fetch data from the API endpoint
        fetch("https://damisaalex.xyz/hngx5/api/videos")
            .then((response) => {
            if (!response.ok) {
                throw new Error("Network response was not ok");
            }
            return response.json();
        })
            .then((data) => {
            setVideos(data);
            setLoading(false);
        })
            .catch((error) => {
            console.error("Error fetching data:", error);
            setLoading(false);
        });
    }, []);
    if (loading) {
        return React.createElement("div", null, "Loading...");
    }
    return (React.createElement("div", { className: "w-[90%] my-[2%] mx-auto flex flex-col gap-10" },
        React.createElement("div", { className: "grid grid-cols-1 sm:grid-cols-2 gap-4" }, videos.map((video, index) => (React.createElement("div", { key: index, className: "w-fit" },
            React.createElement("h1", { className: "text-2xl font-semibold py-4" }, video.title),
            React.createElement("div", { className: "w-full" },
                React.createElement(Link, { to: `/video/${video._id}` },
                    React.createElement(Card, { image: "video2.png", title: video.title, date: video.uploadDate, videoId: video._id })))))))));
}
